export default "header {\n    display: flex;\n    /* border-bottom: 1px solid #f3f3f3; */\n    box-shadow: 0px 0px 12px 0px #ccc;\n    position: relative;\n    z-index: 1;\n}\n\n.navbar-wrapper {\n    /* width: 50px; */\n    width: 40px;\n    line-height: 60px;\n    text-align: center;\n    font-size: 20px;\n}\n\n.navbar-header {\n    flex: 1;\n    display: flex;\n}\n\n.navbar-header span {\n    flex: 1;\n    text-align: right;\n    line-height: 60px;\n    font-size: 20px;\n}\n\ni {\n    cursor: pointer;\n}\n\nbutton,\nbutton:hover,\nbutton:active {\n    outline: none;\n}\n\n/***** User profile ****/\n\n.user-icon {\n    position: relative;\n}\n\n.user-profile {\n    position: absolute;\n    top: 102%;\n    width: 200px;\n    overflow: hidden;\n    border: 1px solid #ccc;\n    right: 10px;\n}\n\n.list-group-item {\n    cursor: pointer;\n}\n\n/*-------------------------------*/\n\n/*       Hamburger-Cross         */\n\n/*-------------------------------*/\n\n.hamburger {\n    display: block;\n    width: 23px;\n    height: 23px;\n    margin-left: 10px;\n    background: transparent;\n    border: none;\n    position: relative;\n    margin-top: 16px;\n}\n\n.hamburger.is-closed:before {\n    content: '';\n    display: block;\n    width: 100px;\n    font-size: 14px;\n    color: #fff;\n    line-height: 32px;\n    text-align: center;\n    opacity: 0;\n    -webkit-transform: translate3d(0, 0, 0);\n    -webkit-transition: all .35s ease-in-out;\n}\n\n/* .hamburger.is-closed:hover:before {\n    opacity: 1;\n    display: block;\n    -webkit-transform: translate3d(-100px, 0, 0);\n    -webkit-transition: all .35s ease-in-out;\n} */\n\n.hamburger.is-closed .hamb-top,\n.hamburger.is-closed .hamb-middle,\n.hamburger.is-closed .hamb-bottom,\n.hamburger.is-open .hamb-top,\n.hamburger.is-open .hamb-middle,\n.hamburger.is-open .hamb-bottom {\n    position: absolute;\n    left: 0;\n    height: 2px;\n    width: 100%;\n}\n\n.hamburger.is-closed .hamb-top,\n.hamburger.is-closed .hamb-middle,\n.hamburger.is-closed .hamb-bottom {\n    background-color: #1a1a1a;\n}\n\n.hamburger.is-closed .hamb-top {\n    top: 5px;\n    -webkit-transition: all .35s ease-in-out;\n}\n\n.hamburger.is-closed .hamb-middle {\n    top: 50%;\n    margin-top: -2px;\n}\n\n.hamburger.is-closed .hamb-bottom {\n    bottom: 5px;\n    -webkit-transition: all .35s ease-in-out;\n}\n\n/* .hamburger.is-closed:hover .hamb-top {\n    top: 0;\n    -webkit-transition: all .35s ease-in-out;\n}\n\n.hamburger.is-closed:hover .hamb-bottom {\n    bottom: 0;\n    -webkit-transition: all .35s ease-in-out;\n} */\n\n.hamburger.is-open .hamb-top,\n.hamburger.is-open .hamb-middle,\n.hamburger.is-open .hamb-bottom {\n    background-color: #1a1a1a;\n}\n\n.hamburger.is-open .hamb-top,\n.hamburger.is-open .hamb-bottom {\n    top: 50%;\n    margin-top: -2px;\n}\n\n.hamburger.is-open .hamb-top {\n    -webkit-transform: rotate(45deg);\n    -webkit-transition: -webkit-transform .2s cubic-bezier(.73, 1, .28, .08);\n}\n\n.hamburger.is-open .hamb-middle {\n    display: none;\n}\n\n.hamburger.is-open .hamb-bottom {\n    -webkit-transform: rotate(-45deg);\n    -webkit-transition: -webkit-transform .2s cubic-bezier(.73, 1, .28, .08);\n}\n\n.hamburger.is-open:before {\n    content: '';\n    display: block;\n    width: 100px;\n    font-size: 14px;\n    color: #fff;\n    line-height: 32px;\n    text-align: center;\n    opacity: 0;\n    -webkit-transform: translate3d(0, 0, 0);\n    -webkit-transition: all .35s ease-in-out;\n}\n\n/* .hamburger.is-open:hover:before {\n    opacity: 1;\n    display: block;\n    -webkit-transform: translate3d(-100px, 0, 0);\n    -webkit-transition: all .35s ease-in-out;\n} */\n/*# sourceMappingURL=data:application/json;base64, */"